<template>
  <FormKit
    type="form"
    :actions="false"
  >
    <FormKitSchema
      v-if="orderSchema"
      :schema="orderSchema"
    />
    <Button label="Добавить" @click="emits('submit', data)" />
  </FormKit>
</template>

<script setup lang="ts">
const emits = defineEmits(['submit']);
const data = ref({
  name: '',
  description: '',
  enabled: false,
});

const orderSchema = ref([
  {
    $formkit: 'primeInputText',
    name: 'name',
    label: 'Имя',
  },
  {
    $formkit: 'primeInputText',
    name: 'description',
    label: 'Описание',
  },
  {
    $formkit: 'primeCheckbox',
    name: 'enabled',
    label: 'Активен',
  },
]);
</script>
